// Import react & styles
import React from 'react';
import './task.css';
// Import icons & ui-components
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import CircleOutlinedIcon from '@mui/icons-material/CircleOutlined';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';
import { IconButton, ButtonGroup } from '@mui/material';

// A single task tag
export default function Task({
  task,
  handleToggle,
  handleDelete,
  handleChange,
}) {
  const handleTogClick = (e) => {
    e.preventDefault();
    handleToggle(e.currentTarget.id, task);
  }; // Handles check button

  const handleDelClick = (e) => {
    e.preventDefault();
    handleDelete(e.currentTarget.id);
  }; // Handles delete button

  return (
    <div id={task.id} key='task' className='todo'>
      {/* Content of the task */}
      <span
        className={
          // Style complete tasks
          task.complete ? 'todo-content complete' : 'todo-content'
        }
        id={task.id} // Id for handlers
        contentEditable='true' // Can edit already written tasks
        value={task.task} // Fixes contentEditable issues
        onBlur={handleChange} // Save changes when stop editing
      >
        {task.task /* Displaying the correct text */}
      </span>

      <ButtonGroup className='todo-actions' key='task-actions'>
        {/* Check button */}
        {task.complete ? (
          // If task is completed
          <IconButton
            id={task.id} // Id for handlers
            onClick={handleTogClick} // Toggles check mark
          >
            <CheckCircleOutlineIcon className='todo-btn check-btn' />
          </IconButton>
        ) : (
          // If task isn't completed
          <IconButton aria-label='check' id={task.id} onClick={handleTogClick}>
            <CircleOutlinedIcon className='todo-btn check-btn' />
          </IconButton>
        )}

        {/* Delete button */}
        <IconButton
          id={task.id} // Id for handlers
          onClick={handleDelClick} // Deletes task
        >
          <RemoveCircleIcon className='todo-btn delete-btn' />
        </IconButton>
      </ButtonGroup>
    </div>
  );
}
